@import '../app/app';


.books-page {
    &__item {
        background-color: whitesmoke;
        display: flex;
        flex-direction: column;
        margin: 0 1.1em 3em 1.1em;
        flex-basis: 250px;
        padding: .8em;
        border-radius: .4em;
        &:hover {
            box-shadow: 0px 0px 14px 3px rgba(214,214,214,1); } } }

.book-label-top {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    max-width: 200px;
    margin-top: 0.4em;
    font-family: $fira-font;
    line-height: 1.2em;
    &__title {
        font-weight: 400;
        font-size: 1.2em; }
    &__author {
        margin-top: 0.4em;
        color: $divider-color; } }

.book-labels {
    align-self: flex-end;
    width: 100%; }


.book-label-bottom {
    display: flex;
    justify-content: space-between;
    margin-top: 1.3em;
    align-self: flex-end;
    align-content: flex-end;
    &__price {
        font-weight: 600;
        font-size: 1.3em;
        letter-spacing: .06em; }
    &__btn {
        border: none;
        border-radius: .3em;
        background-color: $dark-primary;
        color: $text-icons-color;
        padding: .3em .7em;
        box-shadow: 0 2px  4px 0 rgba(0,0,0,.2);
        transition: .2s;
        &:hover {
            opacity: .8; }
        &:active {
            opacity: 1; } } }

.book-img {
    display: block;
    width: 100%;
    height: 100%; }

.kek {
    background-color: red; }
